.projects-container {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  max-width: 1200px;
  margin: 0 auto;
}

.projects-container h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.projects-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row; /* Aligns items in a row */
  gap: 20px; /* Adds space between the items */
  flex-wrap: wrap; /* Ensures items wrap to the next line if they overflow */
}

.project-item {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 20px;
  transition: background-color 0.3s ease;
  flex: 1 1 300px; /* Flex-grow: 1, Flex-shrink: 1, Base width: 300px */
  box-sizing: border-box; /* Ensures padding and border are included in the width */
}

.project-item:hover {
  background-color: #f0f0f0;
}

.project-item h3 {
  margin-top: 0;
  color: #333;
}

.project-item p {
  color: #666;
}

.project-image {
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 10px;
}
